  padding: 10px;
  color: var(--app-color-gray-dark);
}

.addStepTooltip {
  position: relative;
  margin: 0 0 0 40px;
  padding-top: 1px;
  border-radius: 4px;
  background-color: #ffffff;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.4);
  display: inline-block;
  padding: 1em;
  font-family: var(--app-font-gt-walsheim-pro);

  &::after,
  &::before {
    right: 100%;
    top: 50%;
    border: solid transparent;
    content: " ";
    height: 0;
    width: 0;
    position: absolute;
    pointer-events: none;
  }

  &::after {
    border-color: #28d57f00;
    border-right-color: white;
    border-width: 15px;
    margin-top: -15px;
  }

  &::before {
    border-color: #0cf5a700;
    border-right-color: #4a4a4a;
    border-width: 15px;
    margin-top: -15px;
  }

  color: var(--app-color-gray-dark);
}

.messageContainer {
  white-space: normal;
}

.title {
  font-family: var(--app-font-gt-walsheim-pro);
  text-align: center;
  margin: 15px auto;
  font-size: 20px;
  font-weight: bold;
